<template>
  <div class="w-full h-64 flex justify-center" :class="(OddArticle)?'bg-indigo-200': 'bg-blue-200'">
    <div class="w-9/12 max-w-screen-lg ml-10 mr-10">
      <div class="flex" :class="(OddArticle)?'flex-row-reverse': ''">
        <div class="hidden sm:flex h-64 w-64">
          <img
            :src="require(`@/assets/${AvatarSrcComp}`)"
            alt="avatar"
            class="
              hidden
              object-cover
              sm:block
              rounded-full
              pt-4
              pb-4
            "
          />
        </div>

        <div class="flex justify-center w-full min-h-full flex-col p-4">
          <div class="text-center text-xl align-middle overflow-hidden max-h-full">
            <slot class="overflow-hidden">Text will come soon</slot>
          </div>
          <div v-if="BtnUrl" class="flex justify-center mt-5">
            <a
              class="
                hover:bg-blue-400
                bg-blue-700 
                text-white
                text-2xl
                font-bold
                py-2 px-4
                rounded-full
              "
              :href="BtnUrl"
            >
              <slot name="buttonText">Click Me</slot>
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["AvatarSrc", "BtnUrl", "BtnText", "OddArticle"],
  computed: {
    AvatarSrcComp() {
      if (this.AvatarSrc) {
        return this.AvatarSrc;
      } else {
        return "full_logo.jpg";
      }
    },
  },
};
</script>

